import ActionCallback from "@/vendors/particles/actions/ActionCallback";
import BoundingBox from "@/vendors/particles/actions/BoundingBox";
import Move from "@/vendors/particles/actions/Move";
import TurnTowardsPoint from "@/vendors/particles/actions/TurnTowardsPoint";
import ZonedAction from "@/vendors/particles/actions/ZonedAction";
import Emitter from "@/vendors/particles/emitters/Emitter";
import RectangleZone from "@/vendors/particles/zones/RectangleZone";
import { Point } from "pixi.js";
import EventManager from "../events/EventManager";
import { EventType } from "../events/EventType";
import { KeyPoll } from "../KeyPoll";
import * as Keyboard from "../Keyboard";
import GlobalStorage, { AnimateMapConfig, ReplicatedUser } from "../storage/GlobalStorage";
import CustomParticle from "./CustomParticle";
import UpdateUserPositionAction from "./UpdateUserPositionAction";

export default class Movements {
    private hero: ReplicatedUser | null = null;
    private heroParticle: CustomParticle | null = null;
    private heroEmitter: Emitter | null = null;

    private pointed = false;
    private keyboarded = false;
    private destinationZone: RectangleZone | null = null;
    private turnTowardsPoint: TurnTowardsPoint | null = null;
    private zonedAction: ZonedAction | null = null;

    private heroSpeedSqLimitByZoom: Array<number> = [100, 1000, 10000];

    private mainEmitter: Emitter = new Emitter();
    private emitters: Array<Emitter> = [];

    private keyPoll: KeyPoll = new KeyPoll();

    constructor() {
        this.setupMainEmitter();
        this.setupHeroParticle();
        this.setupEventManager();
        this.createHeroControlModule();
    }

    public update(time: number): void {
        if (!this.hero) {
            return;
        }

        this.moveHeroTo();

        for (let i = 0; i < this.emitters.length; i++) {
            this.emitters[i].update(time);
        }
    }

    private setupEventManager(): void {
        EventManager.on(EventType.UPDATE_CAMERA_ZOOM, this._onUpdateZoom, this);
    }

    private _onUpdateZoom(): void {
        if (!this.heroParticle || (this.heroParticle.velX === 0 && this.heroParticle.velY === 0)) {
            return;
        }
        const particle: CustomParticle = this.heroParticle;
        const speed = Math.sqrt(particle.velX * particle.velX + particle.velY * particle.velY);
        const speedLimit = this.getSpeedLimit();
        if (speed != speedLimit) {
            const scale = speedLimit / speed;
            particle.velX *= scale;
            particle.velY *= scale;
        }
    }

    private getSpeedLimit(): number {
        const zoom = GlobalStorage.get("zoom");
        const _limitSq = this.heroSpeedSqLimitByZoom[zoom];
        return Math.sqrt(_limitSq);
    }

    private setupMainEmitter(): void {
        const emitter: Emitter = this.mainEmitter;
        emitter.actions = [
            new Move(),
            //   new SpeedLimit(this.WALKER_DEFAULT_SPEED, false),
            new BoundingBox(
                10,
                10,
                GlobalStorage.get("worldWidth") - 10,
                GlobalStorage.get("worldHeight") - 10
            ),
            new UpdateUserPositionAction()
        ];
        this.emitters.push(emitter);
    }

    private setupHeroParticle(): void {
        this.hero = GlobalStorage.get('hero');
        this.heroParticle = new CustomParticle(this.hero!);
        this.heroParticle.x = this.hero!.x;
        this.heroParticle.y = this.hero!.y;

        this.heroParticle.velX = 10;
        this.heroParticle.velY = 10;

        this.mainEmitter.addParticle(this.heroParticle);
    }

    private pointHeroTo(e): void {
        if (!this.heroParticle || !this.heroEmitter) {
            return;
        }

        this.pointed = true;
        this.keyboarded = !this.pointed;

        if (!this.turnTowardsPoint) {
            this.turnTowardsPoint = new TurnTowardsPoint();
            this.turnTowardsPoint.power = 100;
            this.heroEmitter.addAction(this.turnTowardsPoint);
        }
        this.turnTowardsPoint.x = e.x;
        this.turnTowardsPoint.y = e.y;

        let dx = e.x - this.heroParticle.x;
        let dy = e.y - this.heroParticle.y;
        const length = Math.sqrt(dx * dx + dy * dy);
        dx /= length;
        dy /= length;
        const limit = this.getSpeedLimit();
        this.heroParticle.velX = dx * limit;
        this.heroParticle.velY = dy * limit;

        if (!this.destinationZone) {
            this.destinationZone = new RectangleZone();

            this.zonedAction = new ZonedAction(
                new ActionCallback(() => {
                    this.pointHeroToStop();
                    if (this.heroParticle) {
                        this.heroParticle.velX = 0;
                        this.heroParticle.velY = 0;    
                    }
                }, false), this.destinationZone
            )
            this.heroEmitter.addAction(this.zonedAction);
        }

        const precision = 10;
        this.destinationZone.left = e.x - precision;
        this.destinationZone.top = e.y - precision;
        this.destinationZone.right = e.x + precision;
        this.destinationZone.bottom = e.y + precision;
    }

    private moveHeroTo(): void {
        const zoom = GlobalStorage.get('zoom');
        const speed = Math.sqrt(this.heroSpeedSqLimitByZoom[zoom]);

        let down = false;
        if (!this.heroParticle || !this.heroEmitter) {
            return;
        }
        if (this.keyPoll.isDown(Keyboard.W) || this.keyPoll.isDown(Keyboard.UP)) {
            down = true;
            this.heroParticle.velY = -speed;
        } else if (this.keyPoll.isDown(Keyboard.S) || this.keyPoll.isDown(Keyboard.DOWN)) {
            down = true;
            this.heroParticle.velY = speed;
        } else if (!this.pointed) {
            this.heroParticle.velY = 0;
        }
        if (this.keyPoll.isDown(Keyboard.A) || this.keyPoll.isDown(Keyboard.LEFT)) {
            down = true;
            this.heroParticle.velX = -speed;
        } else if (this.keyPoll.isDown(Keyboard.D) || this.keyPoll.isDown(Keyboard.RIGHT)) {
            down = true;
            this.heroParticle.velX = speed;
        } else if (!this.pointed) {
            this.heroParticle.velX = 0;
        }

        if (down) {
            // end pointed
            this.pointHeroToStop();
        } else if (this.keyboarded) {
            this.keyboarded = false;
            this.heroParticle.velX = 0;
            this.heroParticle.velY = 0;
        }
    }

    private pointHeroToStop(): void {
        this.pointed = false;

        if (!this.heroParticle || !this.heroEmitter) {
            return;
        }

        if (this.turnTowardsPoint) {
            this.heroEmitter!.removeAction(this.turnTowardsPoint);
            this.turnTowardsPoint = null;
        }

        if (this.zonedAction) {
            this.heroEmitter!.removeAction(this.zonedAction);
            this.zonedAction = null;
            this.destinationZone = null;
        }
    }

    private createHeroControlModule(): void {
        this.heroEmitter = new Emitter();
        this.heroEmitter.addParticle(this.heroParticle!);
        this.emitters.push(this.heroEmitter);

        EventManager.on(EventType.POINTERDOWN_ON_VIEWPORT, (e: Point) => {
            console.log('pointHeroTo', e)
            console.log('heroPosition', this.heroParticle?.x, this.heroParticle?.y)
            this.pointHeroTo(e);
        });
    }
}