import { Sprite, Container, Graphics, Point } from "pixi.js";
import GlobalStorage, { ReplicatedUser } from "../storage/GlobalStorage";
export default class MapDebugContainer extends Container {

    private hero: Graphics | null = null;

    constructor() {
        super();
        this.init();
    }

    public init(): void {
        const worldWidth = GlobalStorage.get('worldWidth');
        const worldHeight = GlobalStorage.get('worldHeight');

        this.drawMainOuterRadius();
        this.drawLeftOuterRarius();
        this.drawRightOuterRadius();
    }

    public update(time: number): void {
        this.drawHero();
    }

    private drawHero(): void {
        const hero: ReplicatedUser = GlobalStorage.get('hero');
        if (!hero) {
            return;
        }
        if (!this.hero) {
            this.hero = new Graphics();
            this.addChild(this.hero);
        }

        this.hero.clear();
        this.hero.beginFill(0xff0000);
        this.hero.drawCircle(0, 0, 100);
        this.hero.alpha = .5;
        this.hero.endFill();
        this.hero.x = hero.x;
        this.hero.y = hero.y;
    }

    private drawMainOuterRadius(): void {
        const center: Point = this.getCenter();
        const g: Graphics = new Graphics();
        g.beginFill(0x00ff00);
        g.drawCircle(center.x, center.y, 1600);
        g.endFill();
        g.alpha = .1;
        this.addChild(g);
    }

    private drawLeftOuterRarius(): void {
        const center: Point = new Point(3533, 2151);
        const g: Graphics = new Graphics();
        g.beginFill(0x00ff00);
        g.drawCircle(center.x, center.y, 600);
        g.endFill();
        g.alpha = .1;
        this.addChild(g);
    }

    private drawRightOuterRadius(): void {
        const center: Point = new Point(6141, 7741);
        const g: Graphics = new Graphics();
        g.beginFill(0x00ff00);
        g.drawCircle(center.x, center.y, 800);
        g.endFill();
        g.alpha = .1;
        this.addChild(g);
    }

    private getCenter(): Point {
        const worldWidth = GlobalStorage.get('worldWidth');
        const worldHeight = GlobalStorage.get('worldHeight');
        // return new Point(worldWidth / 2, worldHeight / 2);
        return new Point(5040, 4567);
    }
}
