import { Box } from "js-quadtree";
import { MovedEventData, Viewport, ZoomedEventData } from "pixi-viewport";
import { Application, Container, Sprite } from "pixi.js";
import GlobalStorage, { ReplicatedUser } from "../storage/GlobalStorage";
import * as PIXI from "pixi.js";
import MAP_IMAGE from '../../assets/map.png';
import MapDebugContainer from "./MapDebugContainer";
import EventManager from "../events/EventManager";
import { EventType, EventUpdateCameraRectProps, EventUpdateCameraZoomProps } from "../events/EventType";

export default class MapContainer extends Container {

    private _viewport: Viewport | null = null;
    private _viewportFollowTarget: Container | null = null;
    private _viewportRectMultiplier = 1.2;
    private _background: Sprite | null = null;
    private _debug: MapDebugContainer | null = null;

    constructor(private _app: Application) {
        super();
    }

    public async init(): Promise<void> {
        this.initViewport();
        this.initBackground();
        this.initDebugContainer();
        

        EventManager.on(EventType.UPDATE_CAMERA_ZOOM, this.setupVewportFollowing, this);
        // TODO refactor
        GlobalStorage.set('zoom', 2);
        this._viewport?.setZoom(.2);
        this.setupVewportFollowing();
    }

    public update(time: number): void {
        this._debug?.update(time);

        
        const hero: ReplicatedUser = GlobalStorage.get('hero');
        if (hero && this._viewportFollowTarget) {
            this._viewportFollowTarget.x = hero.x;
            this._viewportFollowTarget.y = hero.y;
        }
    }

    private initViewport(): void {
        const worldWidth = GlobalStorage.get("worldWidth");
        const worldHeight = GlobalStorage.get("worldHeight");

        this._viewport = new Viewport({
            worldWidth: worldWidth,
            worldHeight: worldHeight,
            interaction: this._app.renderer.plugins.interaction,
        })
            .drag({ factor: 0.9 })
            .pinch()
            .wheel({ percent: 0.5, smooth: 10 })
            .decelerate()
            .clamp({
                left: true,
                right: true,
                top: true,
                bottom: true,
            })
            .clampZoom({
                maxWidth: worldWidth,
                maxHeight: worldHeight,
            });

        this._viewport.moveCenter(worldWidth / 2, worldHeight / 2);
        this._viewport.on("moved", this._onViewportMoved, this);
        this._viewport.on("zoomed", this._onViewportZoomed, this);

        // GlobalStorage.set('zoom', this._viewport.zoom);

        this.addChild(this._viewport);
    }

    private setupVewportFollowing(): void {
        const hero: ReplicatedUser = GlobalStorage.get('hero');

        if (!hero) {
            return;
        }

        if (!this._viewportFollowTarget) {
            this._viewportFollowTarget = new Container();
            this._viewportFollowTarget.x = hero.x;
            this._viewportFollowTarget.y = hero.y;
            this.addChild(this._viewportFollowTarget);
        }
        const arr: Array<number> = [100, 1000, 10000];
        const zoom = GlobalStorage.get('zoom');
        const options = {
            radius: Math.sqrt(arr[zoom])
        };
        this._viewport?.follow(this._viewportFollowTarget, options);
    }

    private _onViewportMoved(data: MovedEventData): void {
        const view = data.viewport.lastViewport;
        const x = -view?.x / view?.scaleX;
        const y = -view?.y / view?.scaleY;
        const width = data.viewport.screenWidth / view?.scaleX;
        const height = data.viewport.screenHeight / view?.scaleY;

        const rect = new Box(
            x + (width - width * this._viewportRectMultiplier) / 2,
            y + (height - height * this._viewportRectMultiplier) / 2,
            width * this._viewportRectMultiplier,
            height * this._viewportRectMultiplier
        );

        GlobalStorage.set("cameraRect", rect);

        EventManager.emit(
          EventType.UPDATE_CAMERA_RECT,
          rect as EventUpdateCameraRectProps
        );
    }

    private _onViewportZoomed(data: ZoomedEventData): void {
        const layer = GlobalStorage.get("layer");
        let value = 0;

        if (data.viewport.lastViewport.scaleY > 0.6 && layer !== 0) {
            value = 0;
        } else if (data.viewport.lastViewport.scaleY < 0.2 && layer !== 2) {
            value = 2;
        } else if (layer !== 1) {
            value = 1;
        }

        GlobalStorage.set("zoom", value);

        EventManager.emit(
          EventType.UPDATE_CAMERA_ZOOM,
          value as EventUpdateCameraZoomProps
        );

        this.setupVewportFollowing();
    }

    private initBackground(): void {
        this._background = PIXI.Sprite.from(MAP_IMAGE);

        this._viewport?.addChild(this._background);
    }

    private initDebugContainer(): void {
        this._viewport?.on(
            "click",
            (e) => {
              
              const point: PIXI.Point = e.data.getLocalPosition(
                this._background!,
                e.data.global,
                e.data.global
              )
                // TODO  
              EventManager.emit(EventType.POINTERDOWN_ON_VIEWPORT, point);
            },
            this
          );

        this._debug = new MapDebugContainer();
        this._viewport?.addChild(this._debug);
    }


}