})
export default class Game extends Vue {
    mounted() {
        setTimeout(() => {
            // game(this.$refs.game as HTMLElement);
            new GameInstance(this.$refs.game as HTMLElement);
        }, 100);
    }

    destroyed() {
        console.log('Game', 'destroyed')
    }

    private handleClick(): void {
        console.log(' CLICK')
    }
}
