export class GameInstance {
    private _app: Application | null = null;
    private _renderer: Renderer | null = null;
    private _stage: Container | null = null;
    private _mapContainer: MapContainer | null = null;
    private _movements: Movements | null = null;

    constructor(private _containerElement: HTMLElement) {
        this.init();
    }

    private async init(): Promise<void> {
        await this.initHero();
        await this.initMovements();
        await this.initAssest();
        await this.initRenderer();
        await this.initMap();
        this._app?.ticker.add(this.update, this);
    }

    private update(time: number): void {
        this._movements?.update(time);
        this._mapContainer?.update(time);
    }

    private async initHero(): Promise<void> {
        const worldWidth = GlobalStorage.get('worldWidth');
        const worldHeight = GlobalStorage.get('worldHeight');

        const hero: ReplicatedUser = {
            id: '' + Date.now(),
            type: AnimateMapEntityType.user,
            x: worldWidth / 2,
            y: worldHeight / 2,
            data: {
                videoUrlString: '',//avatars[x % 12],
                avatarUrlString: '',//default_avatars[y % 4],
                dotColor: Math.floor(Math.random() * 16777215),
            }
        }

        GlobalStorage.set('hero', hero);
    }

    private async initMovements(): Promise<void> {
        this._movements = new Movements();
    }

    private async initAssest(): Promise<void> {
        const assets: AssetsLoader = new AssetsLoader();
        await assets.execute();
    }

    private async initRenderer(): Promise<void> {
        this._app = new Application({
          transparent: true,
          antialias: true,
          resizeTo: this._containerElement,
          backgroundColor: 0x10bb99,
          resolution: window.devicePixelRatio,
        });
    
        this._renderer = this._app.renderer;
        this._stage = this._app.stage;
    
        this._containerElement.appendChild(this._app.view);

        this._app.start();
    }

    private async initMap(): Promise<void> {
        this._mapContainer = new MapContainer(this._app!);
    
        await this._mapContainer.init();
    
        this._stage?.addChild(this._mapContainer);
    }
}